export default ({
	data() {
		return {
			shtext: '智能积木  越野四驱车',
			fentext: [],  //分类
			SelectStyle : 0,  //点击分类页选中样式
			id : "fenr1",
		}
	},
	onLoad() {
		uni.request({
			url: "http://ceshi3.dishait.cn/api/category/app_category",
			success: (res) => {
				this.fentext = res.data.data
			}
		})
	},
	methods: {
		ScrollWatch(e) {
			/**  
			 * 滚动监听
			 */
			console.log(e)
			if(e.detail.scrollTop>3300){
				this.SelectStyle = 5
			}else if(e.detail.scrollTop>2640&&e.detail.scrollTop<3300){
				this.SelectStyle = 4
			}else if(e.detail.scrollTop>1980&&e.detail.scrollTop<2640){
				this.SelectStyle = 3
			}else if(e.detail.scrollTop>1320&&e.detail.scrollTop<1980){
				this.SelectStyle = 2
			}else if(e.detail.scrollTop>660&&e.detail.scrollTop<1320){
				this.SelectStyle = 1
			}else{
				this.SelectStyle = 0
			}
			
		},
		handleSelect(e){
			this.id = 'fenr'+parseInt(e+1);
			this.SelectStyle = e;
		}
	}

})
